    box-sizing: border-box;
    position: absolute;
    width: 0;
    height: 0;
    border-left: 3px solid transparent;
    border-right: 3px solid transparent;
    transform: rotate(30deg);
  }

  &::before {
    content: "";
    display: block;
    box-sizing: border-box;
    position: absolute;
    width: 0;
    height: 0;
    border-left: 3px solid transparent;
    border-right: 3px solid transparent;
    transform: rotate(30deg);
    border-bottom: 12px solid;
    left: 0;
    top: 0;
  }

  &::after {
    border-top: 12px solid;
    right: 0;
    bottom: 0;
  }
}

.gg-bookmark {
